export default {
	data() {
		return {
			// 判断刘海屏
			// 状态栏高度：
			statusBarHeight: null,
			isWidth: null,
			
			isIndex: null
		};
	},
	props: {
		tableData: {
			type: [Array, Boolean],
			default() {
				return [];
			}
		},
		tableHead: {
			type: [Array],
			default() {
				return [];
			}
		},
		tableHeadT: {
			type: [Array],
			default() {
				return [];
			}
		},
		centerList: {
			type: [Array],
			default() {
				return [];
			}
		},
	},
	created() {
		uni.getSystemInfo({
			success: res => {
				console.log(res);
				// 给定表格的高度
				this.isWidth = res.screenHeight - 60 + 'px';
				if (res.statusBarHeight > 43) {
					this.statusBarHeight = res.statusBarHeight + 'px';
				} else {
					this.statusBarHeight = 0;
				}
			}
		});
	},
	methods: {
		getIndex(index) {
			this.isIndex = index;
		},
		toCenter(name){
			uni.showToast({
				title:name
			})
		}
	}
};
